
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


let echarts = require('echarts/lib/echarts')

require('echarts/lib/chart/bar')

require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
export default {
data() {
return {
index: 0,
selected: '',
list2: [
{
imgUrl: 'http://xiaoxiangba.com/assets/img/scene.png',
name: '设备操作'
}, {
imgUrl: 'http://xiaoxiangba.com/assets/img/me.png',
name: '设备信息'
}
],
demo2: '设备操作',
data: [],
switchStatus: false
}
},
created(){
this.clickInit()
},
methods: {
clickInit(){
var deviceId = this.$route.query.deviceId
this.$axios.get(`/SmartHome/get_device/${deviceId}`)
.then((res) => {
this.data = res.data[0]
})
},
clickMore() {
var deviceId = this.$route.query.deviceId
this.$router.push({path: 'del_device', query: {deviceId: deviceId}})
},
drawLine() {

let myChart = echarts.init(document.getElementById('myChart'))

myChart.setOption({
title: {text: 'ECharts 入门示例'},
tooltip: {},
xAxis: {
data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋", "袜子"]
},
yAxis: {},
series: [{
name: '销量',
type: 'bar',
data: [5, 20, 36, 10, 10, 20]
}]
});
},
onItemClick (index) {
console.log('on item click:', index)
},
clickCode(id) {
this.$vux.loading.show({
text: 'loading'
})
this.$axios.post('/SmartHome/up_device',{
id: id,
status: '关闭'
})
.then((res) => {
console.log(res)
})
.catch((err) => {
console.log(err)
})
setTimeout(() => {
this.$vux.loading.hide()
}, 3000)
}
},
mounted(){
this.drawLine();
},
components: {
Swiper,
SwiperItem,
Tabbar,
TabbarItem,
XHeader,
XTable,
XButton,
XSwitch,
LoadMore
}
}



