
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
data() {
return {
rootManage: '管理员',
nickName: 'hws',
familyId: '',
admin: [],
role: [],
data: []
}
},
created() {
this.clickInit()
},
methods: {
clickInit(){
var that = this
var id = JSON.parse(localStorage.mydata).user.id
this.$axios.get(`/SmartHome/get_family?id=${id}`)
.then((rs) => {
that.familyId = rs.data[0].id
this.$axios.get(`/SmartHome/get_member?id=${that.familyId}`)
.then((res) => {
that.admin = res.data[0].创建者
that.role = res.data[0].普通成员
})
})
},
clickManage(){
var that = this
this.$router.push({path: '/my_family/roles_manage', query: {familyId: that.familyId}})
}
},
components: {
XHeader,
Group,
Cell
}
}



