
import TODO from '../common/TODO.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
directives: {
TransferDom
},
data() {
return {
list: [
{
"PowerStatus": "1"
},
{
"ID": "SN001",
"Name": "智能热水壶",
"Type": "家电",
"Icon": "http://xiaoxiangba.com/assets/img/waterBettle.png",
"Title": "这是一款......",
"DateTime": "2018-01-18",
"workerTime": "30",
"Functions": {
"BoilMode": {
"Name": "煮水",
"Icon": "http://xiaoxiangba.com/assets/img/waterBettle.png",
"BoilSwitch": "B1",
"LED": "L0"
},
"KeepWarm": {
"Name": "保温",
"Icon": "http://xiaoxiangba.com/assets/img/waterBettle.png",
"WarmSwitch": "K1",
"Attemperation": "95"
},
"Adjustment": {
"Name": "调节",
"Icon": "http://xiaoxiangba.com/assets/img/waterBettle.png",
"AdjustSwitch": "A1",
"Attemperation": "100"
},
"ManualMode": {
"Name": "手动",
"Icon": "http://xiaoxiangba.com/assets/img/waterBettle.png",
"Manual": "M1"
}
}
}],
todoStatus: false,
doStart: false,
show: false,
lightClass: false,
time: 1800,
start: false,
items: [
{
key: '0',
value: '开关'
}, {
key: '1',
value: '煮水'
}, {
key: '2',
value: '保温'
}, {
key: '3',
value: '定时'
}],
demo: 0,
demo2: '0',
menus: {
menu1: 'Take Photo',
menu2: 'Choose from photos'
},
showMenus: false
}
},
created() {
},
methods: {
clickMore() {
this.$router.push('/del_device')
},
showLight() {
var deviceId = this.$route.query.deviceId
this.$vux.loading.show({
text: 'loading'
})
setTimeout(() => {

this.$router.push({path: '/device_detail',query: {deviceId: deviceId}})
this.$vux.loading.hide()
}, 3000)

},
finish () {
this.start = !this.start
},
},
components: {
TODO,
Actionsheet,
Countup,
Countdown,
Cell,
XButton,
XHeader,
XCircle,
XSwitch,
Tabbar,
TabbarItem,
Checker,
CheckerItem
}
}



